.header-color {
	background: #be93c5; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#7bc6cc,
		#be93c5
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#7bc6cc,
		#be93c5
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.avatar-img {
	height: 250px;
	padding-top: 5em;
}

.banner-text {
	background-color: black;
	opacity: 08;
	width: 75%;
	margin: auto;
	border-radius: 10px;
}

.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: white;
}

.banner-text hr {
	border-top: 5px dotted white;
	width: 50%;
	margin: auto;
}

.banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
}

.contact-body {
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 2em;
}

.contact-grid {
	text-align: center;
	justify-content: center;
	width: 80%;
	height: 550px;
	background: white;
	color: black;
	margin-bottom: 20px;
}

.contact-grid h2 {
	font-family: "Anton", sans-serif;
}

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}

.contact-grid p {
	font-family: "Oxygen", sans-serif;
	font-weight: bold;
}

.contact-list i {
	font-size: 66px;
	padding-right: 5rem;
}

.landing-grid {
	background: #be93c5;
	background: -webkit-linear-gradient(to right, #7bc6cc, #be93c5);
	background: linear-gradient(to right, #7bc6cc, #be93c5);
	text-align: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.resume-right-col {
	background: #27221f;
	color: white;
	padding: 2em;
}

.social-links {
	display: flex;
	justify-content: space-between;
	width: 50%;
	margin: auto;
}

.social-links i {
	color: white;
	font-size: 5em;
	padding: 8px;
}
